import { Component, OnInit, inject } from '@angular/core';
import { Router } from '@angular/router';
import { NzAvatarModule } from 'ng-zorro-antd/avatar';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzCardModule } from 'ng-zorro-antd/card';
import { NzDividerModule } from 'ng-zorro-antd/divider';

@Component({
  selector: 'icom-libs-campaign-management-detail-management-templates',
  templateUrl: './templates.component.html',
  standalone: true,
  imports: [NzCardModule, NzAvatarModule, NzDividerModule, NzButtonModule]
})
export class IComLibsCampaignManagementTemplatesComponent implements OnInit {
  protected routerLink = inject(Router);
  constructor() { }

  ngOnInit() {
  }


  protected handlerAddNew(e: Event) {
    e.stopPropagation();
    e.preventDefault();
    this.routerLink.navigateByUrl(`campaign/management/detail?mode=edit`);
  }

}
