
import { Pipe, PipeTransform } from '@angular/core';
import * as dayjs from 'dayjs';

@Pipe({
  name: 'icomCampaignManagementDisplayTimeCampaignPipe',
  standalone: true
})
export class IcomCampaignManagementDisplayTimeCampaignPipe implements PipeTransform {

  transform(campaign: any, typeTime: string): string {
    if(!typeTime) {
      return 'Chưa có thông tin';
    }
    switch (typeTime) {
      case 'end':
        if (campaign.type_end_time === 'UNLIMIT') {
          return 'Vô thời hạn'
        }

        if (campaign.type_end_time === 'SPECTIME') {
          return !campaign.end_time ? 'Chưa có thông tin' :  dayjs(campaign.end_time).format('DD/MM/YYYY HH:mm:ss');
        }

        return 'Chưa có thông tin';

      case 'start':
        if (campaign.type_start_time === 'STARTTIME') {
          return 'Ngày tạo'
        }

        if (campaign.type_start_time === 'SPECTIME') {
          return !campaign.start_time ? 'Chưa có thông tin' :  dayjs(campaign.start_time).format('DD/MM/YYYY HH:mm:ss');
        }

        return 'Chưa có thông tin';

      default: 
        return 'Chưa có thông tin';
    }
  }

}
