import { DatePipe, DecimalPipe, NgFor, NgIf } from '@angular/common';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, EventEmitter, Input, Output, ViewContainerRef, inject } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { FormlyFieldConfig, FormlyFormOptions, FormlyModule } from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzSpinModule } from 'ng-zorro-antd/spin';
import { NzFormModule } from 'ng-zorro-antd/form';
import { NzModalRef, NzModalService } from 'ng-zorro-antd/modal';
import { NzEmptyModule } from 'ng-zorro-antd/empty';
import { NzDescriptionsModule } from 'ng-zorro-antd/descriptions';
import { IComLibsServicesProjectCampaignV3Service } from '@icom/services/projects';
import { combineLatest, lastValueFrom, map } from 'rxjs';
import { NzDropDownModule } from 'ng-zorro-antd/dropdown';
import { Router } from '@angular/router';
import { NzIconModule } from 'ng-zorro-antd/icon';
import { NzTabsModule } from 'ng-zorro-antd/tabs';
import { NzTagModule } from 'ng-zorro-antd/tag';
import { NzInputModule } from 'ng-zorro-antd/input';
import { IComLibsCampaignManagementTemplatesComponent } from './templates/templates.component';
import { NzPageHeaderModule } from 'ng-zorro-antd/page-header';
import { NzStatisticModule } from 'ng-zorro-antd/statistic';
import { IComMessageService } from '@icom/services/common';
import { HttpErrorResponse } from '@angular/common/http';
import { NzPaginationModule } from 'ng-zorro-antd/pagination';
import { IcomCampaignManagementDisplayTimeCampaignPipe } from './pipes/display-time-campaign.pipe';
import { NzDividerModule } from 'ng-zorro-antd/divider';
import { IcomCampaignDetailMissionParseValue } from '../../detail/src/src/mission/add-edit/pipes/parse-value.pipe';
import { IcomLibsHasPermissionDirective } from '@icom/directive';
@Component({
  selector: 'icom-libs-campaign-management-detail-management',
  templateUrl: './management.component.html',
  standalone: true,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NgIf,
    NzSpinModule,
    NzFormModule,
    NzEmptyModule,
    NzDescriptionsModule,
    NgFor,
    NzDropDownModule,
    NzIconModule,
    NzTabsModule,
    NzTagModule,
    DatePipe,
    NzInputModule,
    IComLibsCampaignManagementTemplatesComponent,
    NzButtonModule,
    NzStatisticModule,
    NzPageHeaderModule,
    NzPaginationModule,
    NzIconModule,
    IcomCampaignManagementDisplayTimeCampaignPipe,
    NzDividerModule,
    IcomCampaignDetailMissionParseValue,
    DecimalPipe,
    IcomLibsHasPermissionDirective
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class IComLibsCampaignManagementComponent {
  protected form: FormGroup = new FormGroup({});
  protected fields: FormlyFieldConfig[] = [];
  protected options: FormlyFormOptions = {
    formState: {
      disabled: true,
    },
  };
  protected listDataContributor: any[];
  protected model: any;
  protected isLoading!: boolean;
  protected rows: any[];
  protected pagination: any;

  protected modalService = inject(NzModalService);
  protected viewContainerRef = inject(ViewContainerRef);
  protected cdr = inject(ChangeDetectorRef);
  protected campaignService = inject(IComLibsServicesProjectCampaignV3Service);
  protected routerLink = inject(Router);
  protected IComMessageService = inject(IComMessageService);

  @Input() data: any | undefined;

  @Output() icomFunctionControl: EventEmitter<any> = new EventEmitter<any>();

  constructor() {
    this.listDataContributor = [];
    this.rows = [];
    this.fields =
      [{
        fieldGroupClassName: 'flex w-full items-center',
        fieldGroup: [
          {
            key: 'campaign_name',
            type: 'search-input',
            className: 'basis-8/12 mr-4',
            templateOptions: {
              maxLength: 50,
              placeholder: 'Tìm kiếm theo tên chiến dịch',
            },
          },
          {
            key: 'status',
            type: 'select-custom',
            className: 'basis-4/12 mr-4',
            templateOptions: {
              placeholder: 'Select Status',
              viewValue: 'title',
              labelProp: 'title',
              hideOptional: true,
              valueProp: 'id',
              options: this.campaignService.getListStatus()
                .pipe(
                  map(result => {
                    return result.data;
                  }
                  ))
            },
          },
        ]
      }
      ];
    this.model = {};
  }

  async ngOnInit() {
    combineLatest([
      this.campaignService.getContributor()
    ]
    ).subscribe((results: any) => {
      const [dataContributor] = results;
      this.listDataContributor = dataContributor?.data;
      this.cdr.detectChanges();
    });
    this.loadPage(1);

  }

  protected handlerAddNew(e: Event, data?: any) {
    e.stopPropagation();
    e.preventDefault();

    
    const modal: NzModalRef<IComLibsCampaignManagementTemplatesComponent> = this.modalService.create<IComLibsCampaignManagementTemplatesComponent>({
      nzTitle: 'Thêm chiến dịch',
      nzContent: IComLibsCampaignManagementTemplatesComponent,
      nzMaskClosable: false,
      nzClosable: false,
      nzViewContainerRef: this.viewContainerRef,
      nzWidth: 968,
      nzFooter: [
        {
          label: 'Đóng',
          onClick: () => modal.destroy()
        },
      ]
    });
  }

  protected async handlerChangeStatus(e: Event, data: any, status: string) {
    e.stopPropagation();
    e.preventDefault();
    this.modalService.confirm({
      nzTitle: status === 'INACTIVE' ? `Bạn có muốn tắt hoạt động của chiến dịch ${data.name} không ? ` : `Bạn có bật hoạt động của chiến dịch ${data.name} không ? `,
      nzOkText: 'Đồng ý',
      nzOkType: 'primary',
      nzMaskClosable: false,
      nzClosable: true,
      nzOnOk: async () => {
        try {
          this.isLoading = true;
          data.status = status;
          await lastValueFrom(this.campaignService.addEditCampaign(data));
          this.IComMessageService.showAlertMessageDone('Thao tác thành công');

        } catch (err) {
          console.log(err);
          this.IComMessageService.showAlertMessageError((err as HttpErrorResponse)?.error?.error_message || 'Thao tác thất bại');
        } finally {
          this.isLoading = false;
          this.cdr.detectChanges();
        }
      },
      nzCancelText: 'Hủy bỏ'
    }, 'info');
  }

  protected handlerViewDetail(e: Event, item: any, view: string) {
    e.stopPropagation();
    e.preventDefault();
    this.routerLink.navigateByUrl(`campaign/management/detail?id=${item._id}&mode=${view}`);
  }

  protected async loadPage(page: number) {
    try {
      this.rows = [];
      this.isLoading = true;
      const result = await lastValueFrom(this.campaignService.list({
        page: page || 1,
        limit: 10,
        ...this.model
      }));
     
      if (result && result.data && result.data.items) {
        const rows = result.data.items || [];
        this.rows = rows;
        this.pagination = result.data.pagination;

        return;
      }
      this.rows = [];
      this.pagination = result?.data?.pagination || {};
    } catch (err) {
      console.log(err);
    } finally {
      this.isLoading = false;
      this.cdr.detectChanges();
    }
  }

  protected handlerChangePage(page: number) {
    this.loadPage(page);
  }

  protected handlerSearch(e?: Event) {
    e?.stopPropagation();
    e?.preventDefault();
    this.loadPage(1);
  }
}